import React, { useState, useEffect } from "react";
import Filter from "./components/filter";
import AddPersonForm from "./components/form";
import Persons from "./components/persons";
import axiFunctions from "./services/axiFunctions";
import Notification from "./components/notification";

const App = () => {
  const [persons, setPersons] = useState([]);
  const [newName, setNewName] = useState("");
  const [newNumber, setNewNumber] = useState("");
  const [filtered, setFiltered] = useState(persons);
  const [successMessage, setSuccessMessage] = useState(null);
  const [errorMessage, setErrorMessage] = useState(null);

  useEffect(() => {
    axiFunctions.getAllPersons().then((response) => {
      setFiltered(response);
      setPersons(response);
    });
  }, []); // add "persons" inside the empty array to update contact list when the database changes, even on other browsers
  useEffect(() => {
    document.title = "Erixhens' Phonebook";
  });
  const addContact = (event) => {
    event.preventDefault();
    const contact = {
      name: newName,
      number: newNumber,
    };
    if (
      persons.some(
        (person) =>
          person.name.toUpperCase().trim() === contact.name.toUpperCase().trim()
      )
    ) {
      if (
        window.confirm(
          `${contact.name} already added on the phonebook, do you want to update it?`
        )
      ) {
        const personToUpdate = filtered.find(
          (person) => person.name === newName
        );
        axiFunctions
          .updatePerson(personToUpdate.id, contact)
          .then(() => {
            axiFunctions
              .getAllPersons()
              .then((response) => {
                setFiltered(response);
                setSuccessMessage(`${newName} number updated correctly`);
                setTimeout(() => {
                  setSuccessMessage(null);
                }, 3000);
              })
              .catch(() => {
                setErrorMessage(
                  `Couldn't update ${personToUpdate.name} because it has already been deleted`
                );
                setTimeout(() => {
                  setErrorMessage(null);
                }, 3000);
              });
          })
          .catch(() => {
            setErrorMessage(
              `Couldn't update ${personToUpdate.name} because it has already been deleted`
            );
            setTimeout(() => {
              setErrorMessage(null);
            }, 3000);
          });
        setNewName("");
        setNewNumber("");
      }
    }
    if (!contact.number) {
      setErrorMessage("Please insert a number!");
      setTimeout(() => {
        setErrorMessage(null);
      }, 3000);
    } else {
      axiFunctions
        .createPerson(contact)
        .then((contact) => {
          setFiltered(persons.concat(contact));
          setPersons(persons.concat(contact));
          setSuccessMessage(`${contact.name}'s number added correctly`);
          setTimeout(() => {
            setSuccessMessage(null);
          }, 3000);
        })
        .catch(() => {
          setErrorMessage(
            `Couldn't update "${contact.name}" because it has already been deleted`
          );
          setTimeout(() => {
            setErrorMessage(null);
          }, 3000);
        });
      setNewName("");
      setNewNumber("");
    }
  };
  const removeContact = (id) => {
    const personToDelete = filtered.find((person) => person.id === id);
    if (
      window.confirm(`Are you sure you want to delete ${personToDelete.name}?`)
    ) {
      axiFunctions
        .deletePerson(personToDelete.id)
        .then(() => {
          setFiltered(filtered.filter((person) => person.id !== id));
          setErrorMessage(`"${personToDelete.name}" deleted!`);
          setTimeout(() => {
            setErrorMessage(null);
          }, 3000);
        })
        .catch(() => {
          setErrorMessage(
            ` Couldn't delete ${personToDelete.name} as it has already been deleted`
          );
          setTimeout(() => {
            setErrorMessage(null);
          }, 3000);
        });
    }
  };

  const handleNameChange = (event) => {
    setNewName(event.target.value);
  };
  const handleNumberChange = (e) => {
    setNewNumber(e.target.value);
  };
  const filterContacts = (e) => {
    setFiltered(
      persons.filter((person) =>
        person.name.toUpperCase().includes(e.target.value.toUpperCase())
      )
    );
  };

  return (
    <>
      <h1>Phonebook</h1>
      <Notification message={successMessage} type={"success"} />
      <Notification message={errorMessage} type={"error"} />
      <Filter filterHandler={filterContacts} />
      <AddPersonForm
        addContact={addContact}
        newName={newName}
        handleNameChange={handleNameChange}
        newNumber={newNumber}
        handleNumberChange={handleNumberChange}
      />
      <h2>Numbers</h2>
      <Persons persons={filtered} removeFunction={removeContact} />
      ...
    </>
  );
};

export default App;
